<template>
  <div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="name" label="账号" align="center" />
      <el-table-column prop="ip" label="ip地址" align="center" />
      <el-table-column prop="area" label="登录地址" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" />
      <el-table-column prop="uptime_date" label="登录时间" align="center" />
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      tableData: [],
      count: 0,
      page_curren: 1,
      data: {
        config: {
          status,
        },
      },
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fecth('/record/login/search', {
        page_curren: this.page_curren,
      })
        .then(r => {
          this.loading = false
          this.tableData = r.data.list
          this.data = r.data
          this.count = r.data.count
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
  },
}
</script>
<style lang="less" scoped>
.searchs {
  background-color: #f2f2f2;
  border-left: 5px solid #1aa094;
  padding: 14px 0 14px 15px;
  color: #606266;
  font-size: 16px;

  .form-search {
    /deep/ .el-input__inner {
      width: 150px;
    }

    /deep/ .el-range-editor.el-date-editor--daterange {
      width: 300px;
    }

    .data-input {
      width: 300px;
    }
  }
}

.pagination {
  text-align: right;
  margin-top: 20px;
}

.list {
  width: 100%;
  margin-top: 20px;
  text-align: center;

  /deep/ .el-table__header-wrapper {
    th {
      background-color: #f2f2f2;
    }
  }
}
</style>
